@charset "UTF-8";

@font-face {
    font-family: 'icomoon';
    src:    url('../fonts/icomoon.eot?b0e0m1');
    src:    url('../fonts/icomoon.eot?b0e0m1#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?b0e0m1') format('truetype'),
    url('../fonts/icomoon.woff?b0e0m1') format('woff'),
    url('../fonts/icomoon.svg?b0e0m1#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

